import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/views/layout/index.vue'

Vue.use(Router)

const router = new Router({
    // mode: 'history',
    // base: process.env.BASE_URL,
    routes: [
        {
            path: '/',
            redirect: '/dashboard',
            component: Layout,
            children: [
                {
                    path: '/dashboard',
                    component: () => import('@/views/Dashboard.vue'),
                    name: 'Dashboard',
                    meta: {title: '数据统计'}
                },
                {
                    path: '/member_management',
                    component: () => import('@/views/layout/Administer/member_management.vue'),
                    name: 'Member_management',
                    meta: {title: '党员管理'}
                },
                {
                    path: '/propaganda',
                    component: () => import('@/views/layout/Administer/propaganda.vue'),
                    name: 'Propaganda',
                    meta: {title: '党建宣传'}
                },
                {
                    path: '/member_demonstration',
                    component: () => import('@/views/layout/Administer/member_demonstration.vue'),
                    name: 'Member_demonstration',
                    meta: {title: '党员示范'}
                },
                {
                    path: '/volunteer_service',
                    component: () => import('@/views/layout/Administer/volunteer_service.vue'),
                    name: 'Volunteer_service',
                    meta: {title: '志愿服务'}
                },
                {
                    path: '/administer',
                    component: () => import('@/views/Administer.vue'),
                    name: 'Administer',
                    meta: {title: '乡村治理'}
                },
                {
                    path: '/cultural',
                    component: () => import('@/views/Cultural.vue'),
                    name: 'Cultural',
                    meta: {title: '乡村文旅'}
                },
                {
                    path: '/markDown',
                    component: () => import('@/views/MarkDown.vue'),
                    name: 'MarkDown',
                    meta: {title: '富文本编辑器'}
                },
            ],
        },
        {
            path: '/bigscreen',
            component: () => import('@/views/Bigscreen.vue'),
            name: '可视化面板',
        }
    ]
})

router.beforeEach((to, from, next) => {
    next()
})

export default router